import React from 'react';
import testImage from './byczek.png';
import { CodeBlock, CopyBlock, github } from 'react-code-blocks';
import './App.css';

export function Code({code, lang} : {code: string, lang: string}) {
  const block = {text: code,
    language: lang,
    showLineNumbers: true,
    startingLineNumber: 1,
    theme: github
  }
  return (
    <div className='Code'>
    <CopyBlock {...block}/>
    </div>
    );
}

export function Image({path, caption}: {path: string, caption: string}) {

  function enlargeImage()  {
    var image = document.getElementById(path)
    if (image?.classList.contains("ImageEnlarged") ){
      image.classList.remove("ImageEnlarged")
    }
    else {
      image?.classList.add("ImageEnlarged");
    }
  }

  return (
    <div className='Image'>
      <img id={path} alt={path} src={path} onClick={enlargeImage} />
      <p>{caption}</p>
    </div>
  );
}

export function Header({text}: {text: string}) {
  return (
    <h3 className="Header">
    <p>{text}</p>
    </h3>);
}

export function Title({text, undertext}: {text: string, undertext: string}) {
  return (
    <h1 className="Title">
    <p>{text}</p>
    <p className="Undertext">{undertext}</p>
    </h1>);
}

export function Paragraph({text}: {text: string}) {
  return (
    <p className="Paragraph">
    {text}
    </p>
    );
}

export function PageDate({date}: {date: Date}) {
  return (
    <p className="Date">
    {date.getDay()}/{date.getMonth()}/{date.getFullYear()}
    </p>
    );
}

